}

body {
  background-color: #e9e3b4c5;
  width: 100%;
  height: auto;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  display: flex;
  overflow: hidden auto;
}

.header {
  z-index: 10;
  backdrop-filter: blur(6px);
