  box-shadow: 0 4px 12px #f4aa00e0;
}

.filter-btn {
  cursor: pointer;
  backdrop-filter: 5px;
  background-color: #d78408cc;
  border: none;
  border-radius: 15%;
  margin: 10px;
  padding: 12px;
}

.filter-btn:hover {
  transform: scale(1.1);
  box-shadow: 0 0 8px #ef1414e0;
}

.shimmer-container {
  flex-wrap: wrap;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.shimmer-card {
  background: #f6f7f8;
  border-radius: 12px;
  width: 250px;
  height: 300px;
  position: relative;
  overflow: hidden;
}

.shimmer-card:after {
  content: "";
  background: linear-gradient(90deg, #fff0 0%, #fff9 50%, #fff0 100%);
  width: 150px;
  height: 100%;
  animation: 1.2s linear infinite shimmer;
  position: absolute;
  top: 0;
  left: -150px;
}

@keyframes shimmer {
  100% {
    transform: translateX(400px);
  }
}

.filter {
  border-radius: 16px;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin: 10px;
