  justify-content: center;
  margin-top: 0;
  display: flex;
}

.search-input {
  color: #060606;
  background-color: #ffe19070;
  border: 2px solid #333;
  border-radius: 10px 0 0 10px;
  outline: none;
  width: 76%;
  height: 47px;
  padding: 12px;
  font-size: 16px;
  transition: border .3s, box-shadow .3s;
}

.search-input:focus {
  border-color: #ff9800;
