}

.search-button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #ff732d, #ff9800);
  border: none;
  border-radius: 0 10px 10px 0;
  width: 80px;
  height: 47px;
  margin-left: 0;
  padding: 12px 20px;
  font-weight: 600;
  transition: background .3s, transform .2s;
}

.search-button:hover {
  cursor: pointer;
  background: linear-gradient(135deg, #ff9800, #ff5722);
}

.filter-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: 600;
  transition: background-color .3s, transform .2s;
}

.filter-btn:hover {
  background-color: #388e3c;
  transform: scale(1.05);
}

.menu-container {
  gap: 20px;
  width: 100%;
  margin: auto;
  padding: 20px;
  font-family: sans-serif;
  display: flex;
}

.restaurant-name {
