}

.menu-card:hover {
  background: linear-gradient(to bottom right, #e2b57b69, #e1464651);
  transform: translateY(-5px);
  box-shadow: 0 12px 24px #000000b0;
}

.item-name {
  margin-bottom: 10px;
  font-size: 1.1rem;
  font-weight: 600;
}

.item-description {
  color: #413f3f;
