  box-shadow: 8px 8px 8px #1e1b0693;
}

.aboutPage {
  display: flex;
}

.avatar {
  width: 100px;
  height: 100px;
  position: relative;
}

:root {
  --bg1: #f94a0a9f;
  --bg2: #984a027f;
  --bg3: #042196cd;
  --card-bg: #ffffff14;
  --card-border: #fff3;
  --text: #e5e7eb;
  --muted: #cbd5e1;
  --primary: #a3a108;
  --primary-ink: #0a0f0a;
}

.offline-root {
  z-index: 9999;
  backdrop-filter: blur(4px);
  place-items: center;
  padding: 24px;
  display: grid;
  position: fixed;
  inset: 0;
}

.offline-bg {
  background: radial-gradient(1200px 800px at 10% 10%, #ffffff17, transparent 60%), radial-gradient(1000px 700px at 90% 90%, #ffffff0f, transparent 60%), linear-gradient(120deg, var(--bg1), var(--bg2), var(--bg3));
  filter: saturate(1.2);
  background-size: 300% 300%;
  animation: 10s infinite gradientShift;
  position: absolute;
  inset: 0;
}

@keyframes gradientShift {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.offline-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  width: min(680px, 92vw);
  color: var(--text);
  backdrop-filter: blur(10px) saturate(140%);
  border-radius: 24px;
  padding: 28px 28px 22px;
  position: relative;
  box-shadow: 0 20px 60px #00000059, inset 0 1px #ffffff14;
}

.offline-icon {
  border: 1px solid var(--card-border);
  background: #ffffff14;
  border-radius: 16px;
  place-items: center;
  width: 64px;
  height: 64px;
  margin-bottom: 12px;
  font-size: 48px;
  line-height: 1;
  animation: 3s ease-in-out infinite float;
  display: grid;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-6px);
  }
}

.offline-title {
  letter-spacing: .3px;
  margin: 0 0 6px;
  font-size: clamp(24px, 3.2vw, 34px);
}

.offline-sub {
  color: var(--muted);
  margin: 0;
  font-size: 15px;
}

.offline-chips {
  flex-wrap: wrap;
  gap: 10px;
  margin: 18px 0 12px;
  display: flex;
}

.chip {
  border: 1px solid var(--card-border);
  color: var(--text);
  background: #ffffff0f;
  border-radius: 999px;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  font-size: 13px;
  display: inline-flex;
}

.chip-danger {
  background: #dc262626;
  border-color: #dc262659;
}

.chip .dot {
  background: #ef4444;
  border-radius: 999px;
  width: 8px;
  height: 8px;
  box-shadow: 0 0 12px #ef4444;
}

.offline-actions {
  flex-wrap: wrap;
  gap: 12px;
  margin: 12px 0 4px;
  display: flex;
}

.btn {
  appearance: none;
  cursor: pointer;
  border: 0;
  border-radius: 12px;
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 600;
  transition: transform 60ms, box-shadow .2s, background .2s;
}

.btn-primary {
  color: var(--primary-ink);
  background: linear-gradient(#34d399, #10b981);
  box-shadow: 0 10px 20px #10b98159;
}

.btn-primary:hover {
  transform: translateY(-1px);
}

.btn-primary:active {
  transform: translateY(0);
}

.btn-ghost {
  color: var(--text);
  border: 1px solid var(--card-border);
  backdrop-filter: blur(4px);
  background: none;
}

.btn-ghost:hover {
  background: #ffffff0f;
}

.offline-tips {
  color: var(--muted);
  margin-top: 10px;
  font-size: 13px;
}

.offline-tips ul {
  margin: 8px 0 0 18px;
}

.offline-tips li {
  margin: 4px 0;
}

@media (prefers-reduced-motion: reduce) {
  .offline-bg, .offline-icon {
    animation: none;
  }
}

